.list{
    
    width:80%;
    background-color: teal;
    height:50px;
    color: white;
    border-radius: 5px;
    margin:10px auto;
    padding:5px;
}
.list p{
       padding:10px;
       margin:5px 5px 5px 5px;

}

.faicons{
    float: right;
    padding-right: 10px;
    cursor: pointer;
    
}
.faicons1{
    float:right;
    padding-left:10px;
    cursor: pointer;
    
}
.list input{
background-color: transparent;
color:white;
font-size: 18px;
border:0;
}

.list input:focus{
    outline:0;
}