body{
  background-color: black;
}

.container{
  background-color: whitesmoke;
  margin:50px auto;
  width:80%;
  min-height:500px;

}
.containerform {
  width:100%;
  height:100px;
  background-color: whitesmoke;
  margin:10px auto;
  padding:10px;

}
form input{
  padding:5px;
  width:70%;
  height: 40px;
  margin:10px;
  border: 0px ;
  background-color: orange;
  border-radius: 5px;
  color: black;
  font-size: x-large;

}
form input:focus{
outline: none;
}

form button{
  margin:20px 20px;
  padding:5px;
  float:right;
  width:15%;
  text-align: center;
  background-color: grey;
  border-radius:5px;
  
}

